.what-we-do-section {
    background-color: var(--color-tartiary);
    height: 901px;
    position: relative;
    padding: 96px 0;
}

.functions-list {
    margin-left: 76px;
    max-width: 460px;
    margin-top: 32px;
}

.function-item__title {
    font-weight: 700;
    color: var(--color-primary);
    font-size: 24px;
    margin: 0;
    position: relative;
    &:not(:first-child) {
        margin-top: 8px;
    }
}
.function-item__title.title1::before {
    @include before-svg;
    background-image: url('../img/title-icon1.svg');
}
.function-item__title.title2::before {
    @include before-svg;
    background-image: url('../img/title-icon2.svg');
}
.function-item__title.title3::before {
    @include before-svg;
    background-image: url('../img/title-icon3.svg');
}
.function-item__title.title4::before {
    @include before-svg;
    background-image: url('../img/title-icon4.svg');
}
.what-we-do-img {
    display: block;
    margin-top: 147px;
}


