.video-section {
    height: 788px;
    padding-top: 96px;
}





.learn-more__btn {
    margin-top: 48px;
    padding: 16px 32px;
    background-color: #70C174;
    transition: 0.2s background-color;
    border-radius: 4px;
    border:none;
    font-weight: medium;
    color: var(--color-white);
    cursor: pointer;
    &:hover {
        transition: 0.2s background-color;
        background-color: #43a148;
    }
}
.our-supporters {
    display: block;
    margin: 0;
    overflow: hidden;
    margin-top: 64px;
    margin-bottom: 64px;

}
.our-supporters__title {
    width: 90%;
    font-weight: 600;
    letter-spacing: 2px;
    text-transform: uppercase;
    position: relative;
    margin: 0;
    color: var(--color-primary);
    &::after {
        display: block;
        content: '';
        position: absolute;
        width: 100%;
        border-bottom: 1px solid #E5E5E5;
        top: 50%;
        left: 184px;
    
    }
}
.our-supporters__items {
    margin-top: 33px;
    display: flex;
    justify-content: space-between;
}