.projects {
    min-height: 904px;
    background-color: #fff;
    padding: 96px 0;
}
.projects-title {
    margin-left: 109px;
    
    margin-bottom: 64px;
}
.projects-title .top-descr{
    margin-bottom: 32px;
}
.projects-title h3 {
    margin: 0;
    color: var(--color-primary);
    font-weight: bold;
    font-size: 48px;
    max-width: 640px;
}

.project-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    text-align: center;
    margin-left: 12px;

}
.card {
    height: 421px;
    width: 411px;
    position: relative;
    border-radius: 4px;

    text-align: left;
        &::after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background: rgba(11, 7, 6, .5);
            z-index: 2;
            border-radius: 12px;
          }
    
}
.card:nth-child(1){
    background-image: url(../img/project-bg1.jpg);
}
.card:nth-child(2){
    background-image: url(../img/project-bg2.jpg);
}
.card:nth-child(3){
    background-image: url(../img/project-bg3.jpg);
}
.card-content {
    top: 80px;
    left: 48px;
    right: 48px;
    bottom: 48px;
    position: absolute;
    color:var(--color-white);
    z-index: 3;
}
.card-title {
    font-size: 28px;
} 
.card-text {
    margin-bottom: 64px;
}
