html {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

img {
  max-width: 100%;
}

h1 {
  margin: 0;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
  font-weight: 500;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 110px;
  max-width: 90%;
  min-height: 100%;
}

.container-pix {
  margin: 0 auto;
  max-width: 1280px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.section__header {
  margin-bottom: 32px;
  font-weight: 700;
  font-size: 48px;
  color: var(--color-primary);
}
.section__text {
  font-weight: 400;
  font-size: 16px;
  color: #525560;
  line-height: 160%;
}

.visual-content {
  margin-left: 96px;
  width: 480px;
  height: 578px;
  
  iframe {
      border-radius: 12px;
  }
}


.btn-white {
  padding: 16px 32px;
  font-size: 16px;
  font-weight: 500;
  background-color: var(--color-white);
  transition: 0.2s ease-in-out;
  color: var(--color-black);
  border-radius: 4px;
  border:none;
  &:hover{
    transition: 0.2s ease-in-out;
    background-color: var(--color-hover);
  }
}
.section-content__wrapper {
  margin-left: 96px;
  max-width: 704px;
}

.top-descr {
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--color-primary);
  position: relative;
  &::before {
    content: '';
    border-bottom: 1px solid #000;
    position: absolute;
    width: 72px;
    top: 50%;
    left: -96px;
  }
}
.flex-sb {
  width: 100%;
  display: flex;
}
